.home-page-content{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;


    //Events
    &__events{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
        padding: 5rem;
        width: 90%;


        h1{
            font-weight: bold;
            letter-spacing: .2rem;
            font-size: 4rem;
            margin-bottom: 3rem;
            font-family: $fontAudio;
            color: white;
            text-shadow: 0 .1rem 1rem rgba(0, 0, 0, 0.767);
        }

        &__logos{
            width: 100%;
            display: flex;
            justify-content: center;
            flex-wrap: wrap;

            //logo card

            &__card{
                margin: 2rem;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content:space-evenly;
                background-color: rgb(255, 255, 255);
                box-shadow: 0 1.5rem 3rem rgba(0, 0, 0, 0.363);
                width: 24%;
                height: 33rem;
                &:hover{
                    transform: scale(1.05);
                    box-shadow: 0 1.5rem 4rem rgba(0, 0, 0, 0.562);
                }
                img{
                    width: 65%;
                    height: auto;
                }
                .bg-black{
                    width: 100%;
                    height: auto;
                    filter:  brightness(3) invert(1) !important;
                }
                .bg-size{
                    width: 90rem;
                    height: auto;
                }
               
                p{  
                    font-size: 2rem;
                    text-align: center;
                    font-weight: bold;
                    color: $color-black;
                    letter-spacing: .4rem;
                    text-shadow: 0 .1rem 1rem rgba(0, 0, 0, 0.363);
                }
            }
        }
    }
    .bg-dark{
        width: 100%;
        clip-path: polygon(0 6%, 100% 0, 100% 94%, 0% 100%);
        padding: 10rem;
        background-color: $color-black !important;
    }
    //Corporate
    &__corporate{
        width: 100%;
        padding: 5rem;
        display: flex;
        align-items: center;
        justify-content: space-evenly;
        flex-direction: column;
        clip-path: polygon(0 6%, 100% 0, 100% 94%, 0% 100%);
        height: 80rem;
        background-color: $color-black;
        
        h1{ 
            width: 80%;
            font-size: 4rem;
            text-align: center;
            font-weight: 500;
            text-shadow: 0 .1rem 2rem  rgba($color-blue,.3);
            letter-spacing: .4rem;
            font-family: $fontAudio;
            color: white !important;
        }
        
        &__text{
            width: 90%;
            display: flex;
            flex-wrap: wrap;
            justify-content: center;
            font-size: 3rem;

            h5{ 
                display: flex;
                align-items: center;
                justify-content: center;
                margin: 2rem;
                width: 20%;
                letter-spacing: .2rem;
                font-weight: 400;
                color: $color-blue;
                text-align: center;
                padding: 4rem;
                box-shadow: 0 .1rem 2rem rgba(white,.3);


                &:hover{
                    transform: scale(1.05);
                    box-shadow: 0 1.5rem 4rem rgba(0, 0, 0, 0.562);
                }
            }
        }
    }

    //Form
    &__form{
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 5rem;
        margin-top:10rem ;
        width: 80%;
        height: 65rem;
        filter: drop-shadow(0px 20px 50px rgba(0, 0, 0, 0.794));
        clip-path: polygon(0 0, 100% 5%, 100% 100%, 0 96%);
        background-image:linear-gradient(112deg,$color-blue 50%,$color-black 50%) ;
    
        h1{
            font-family: $fontAudio;
            color: white;
            font-size: 8rem;
            margin:2rem ;
            text-shadow: 0 .1rem 2rem rgba(0, 0, 0, 0.486);
        }


        &__group{
            width: 40%;
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 1rem;
            border:.1rem solid rgb(58, 58, 58);
            background-color: rgb(255, 255, 255);
            margin: 1.5rem;

            .icon{
                color: $color-black;
                font-size: 3rem;
            }
            
            input{
                border: none;
                text-align: center;
                width: 90%;
                outline: none;
                height: 5rem;
                font-size: 2.5rem;
            }
        }

        .btn-join{
            margin-top:5rem ;
            background-color:white;
            font-size: 3rem;
            box-shadow: 5;
            padding: 2rem 5rem;
            text-decoration: none;
            color: $color-black;
            border-radius: 4rem;
        }
    }
}