@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Audiowide&display=swap);
*,*:before,*:after{box-sizing:border-box}html,body,div,span,object,iframe,figure,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,code,em,img,small,strike,strong,sub,sup,tt,b,u,i,ol,ul,li,fieldset,form,label,table,caption,tbody,tfoot,thead,tr,th,td,main,canvas,embed,footer,header,nav,section,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline;text-rendering:optimizeLegibility;-webkit-font-smoothing:antialiased;-webkit-text-size-adjust:none;-moz-text-size-adjust:none;text-size-adjust:none}footer,header,nav,section,main{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}input{-webkit-appearance:none;border-radius:0}*{transition:all .3s}html{font-size:62.5%;scroll-behavior:smooth}body{background-image:linear-gradient(-100deg, #16cef9 65%, white 20%);font-family:"Roboto",sans-serif}.active-nav{color:#fff !important}.active-user{color:#f38406}::-webkit-scrollbar{width:1rem}::-webkit-scrollbar-track{background:#111214}::-webkit-scrollbar-thumb{background:#fff}::-webkit-scrollbar-thumb:hover{background:#8a8a8a}.header{width:100%;background-color:#111214;height:auto;position:relative;padding:5rem;display:flex;align-items:center}.header img{width:60%;transform:scale(1);height:auto;animation:imgScale 10s linear 2s infinite alternate}.header h1{position:absolute;width:40%;right:3%;text-align:right;font-weight:bold;letter-spacing:.2rem;line-height:1.2;transform:scale(0.9);color:#16cef9;font-size:8rem;text-shadow:0 0.2rem 2rem rgba(255,255,255,0.219);animation:textScale 15s linear 2s infinite alternate}.header h1 span{color:white}@keyframes imgScale{from{transform:scale(0.9)}to{transform:scale(1)}}@keyframes textScale{from{right:5%}to{right:15%}}.navbar{width:100%;display:flex;justify-content:space-between;padding:2rem;align-items:center;background-color:#111214}.navbar img{width:20%;width:17rem;height:auto}.navbar__list{width:50%;display:flex;justify-content:space-evenly}.navbar__list__item{font-size:1.8rem;font-weight:400;font-family:"Audiowide",cursive;color:#16cef9;text-decoration:none}.navbar__list__item:hover{color:wheat}.footer{width:100%;margin-top:15rem;background-color:#111214;-webkit-clip-path:polygon(2% 10%, 100% 0, 100% 100%, 0% 100%);clip-path:polygon(2% 10%, 100% 0, 100% 100%, 0% 100%);height:40rem;display:flex;flex-direction:column;justify-content:space-between;align-items:center}.footer img{width:45%;opacity:.1}.footer .arrowIcon{position:fixed;right:2%;bottom:10%;z-index:999999999999999;font-size:7rem;color:white}.footer__icons{width:10%;display:flex;justify-content:space-evenly;font-size:4rem}.footer__icons a{text-decoration:none;color:white}.footer__icons .icon{text-align:0 .1rem 2rem black}.footer__icons .icon:hover{transform:scale(1.1)}.footer__copyright{width:80%;display:flex;justify-content:center;align-items:flex-start;height:5rem;color:white;font-size:1.6rem;font-family:"Audiowide",cursive;border-top:0.1rem solid rgba(0,0,0,0.425);padding:2rem}.home-page-content{display:flex;flex-direction:column;align-items:center;justify-content:space-evenly}.home-page-content__events{display:flex;flex-direction:column;align-items:center;justify-content:space-evenly;padding:5rem;width:90%}.home-page-content__events h1{font-weight:bold;letter-spacing:.2rem;font-size:4rem;margin-bottom:3rem;font-family:"Audiowide",cursive;color:white;text-shadow:0 0.1rem 1rem rgba(0,0,0,0.767)}.home-page-content__events__logos{width:100%;display:flex;justify-content:center;flex-wrap:wrap}.home-page-content__events__logos__card{margin:2rem;display:flex;flex-direction:column;align-items:center;justify-content:space-evenly;background-color:#fff;box-shadow:0 1.5rem 3rem rgba(0,0,0,0.363);width:24%;height:33rem}.home-page-content__events__logos__card:hover{transform:scale(1.05);box-shadow:0 1.5rem 4rem rgba(0,0,0,0.562)}.home-page-content__events__logos__card img{width:65%;height:auto}.home-page-content__events__logos__card .bg-black{width:100%;height:auto;-webkit-filter:brightness(3) invert(1) !important;filter:brightness(3) invert(1) !important}.home-page-content__events__logos__card .bg-size{width:90rem;height:auto}.home-page-content__events__logos__card p{font-size:2rem;text-align:center;font-weight:bold;color:#111214;letter-spacing:.4rem;text-shadow:0 0.1rem 1rem rgba(0,0,0,0.363)}.home-page-content .bg-dark{width:100%;-webkit-clip-path:polygon(0 6%, 100% 0, 100% 94%, 0% 100%);clip-path:polygon(0 6%, 100% 0, 100% 94%, 0% 100%);padding:10rem;background-color:#111214 !important}.home-page-content__corporate{width:100%;padding:5rem;display:flex;align-items:center;justify-content:space-evenly;flex-direction:column;-webkit-clip-path:polygon(0 6%, 100% 0, 100% 94%, 0% 100%);clip-path:polygon(0 6%, 100% 0, 100% 94%, 0% 100%);height:80rem;background-color:#111214}.home-page-content__corporate h1{width:80%;font-size:4rem;text-align:center;font-weight:500;text-shadow:0 0.1rem 2rem rgba(22,206,249,0.3);letter-spacing:.4rem;font-family:"Audiowide",cursive;color:white !important}.home-page-content__corporate__text{width:90%;display:flex;flex-wrap:wrap;justify-content:center;font-size:3rem}.home-page-content__corporate__text h5{display:flex;align-items:center;justify-content:center;margin:2rem;width:20%;letter-spacing:.2rem;font-weight:400;color:#16cef9;text-align:center;padding:4rem;box-shadow:0 0.1rem 2rem rgba(255,255,255,0.3)}.home-page-content__corporate__text h5:hover{transform:scale(1.05);box-shadow:0 1.5rem 4rem rgba(0,0,0,0.562)}.home-page-content__form{display:flex;flex-direction:column;align-items:center;padding:5rem;margin-top:10rem;width:80%;height:65rem;-webkit-filter:drop-shadow(0px 20px 50px rgba(0,0,0,0.794));filter:drop-shadow(0px 20px 50px rgba(0,0,0,0.794));-webkit-clip-path:polygon(0 0, 100% 5%, 100% 100%, 0 96%);clip-path:polygon(0 0, 100% 5%, 100% 100%, 0 96%);background-image:linear-gradient(112deg, #16cef9 50%, #111214 50%)}.home-page-content__form h1{font-family:"Audiowide",cursive;color:white;font-size:8rem;margin:2rem;text-shadow:0 0.1rem 2rem rgba(0,0,0,0.486)}.home-page-content__form__group{width:40%;display:flex;justify-content:center;align-items:center;padding:1rem;border:0.1rem solid #3a3a3a;background-color:#fff;margin:1.5rem}.home-page-content__form__group .icon{color:#111214;font-size:3rem}.home-page-content__form__group input{border:none;text-align:center;width:90%;outline:none;height:5rem;font-size:2.5rem}.home-page-content__form .btn-join{margin-top:5rem;background-color:white;font-size:3rem;box-shadow:5;padding:2rem 5rem;text-decoration:none;color:#111214;border-radius:4rem}

