.navbar{
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 2rem;
    align-items: center;
    background-color: $color-black;

    img{
        width: 20%;
        width: 17rem;
        height: auto;
    }

    &__list{
        width: 50%;
        display: flex;
        justify-content: space-evenly;

        &__item{
            font-size: 1.8rem;
            font-weight: 400;
            font-family: $fontAudio;
            color: $color-blue;
            text-decoration: none;

            &:hover{
                color: wheat;
            }
        }
    }
}