.footer{
    width: 100%;
    margin-top: 15rem;
    background-color:$color-black;
    clip-path: polygon(2% 10%, 100% 0, 100% 100%, 0% 100%);
    height: 40rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    img{
        width: 45%;
        opacity: .1;
    }
    .arrowIcon{
        position: fixed;
        right: 2%;
        bottom: 10%;
        z-index: 999999999999999;
        font-size: 7rem;
        color: white;
    }

    &__icons{
        width: 10%;
        display: flex;
        justify-content: space-evenly;
        font-size: 4rem;
        a{
            text-decoration: none;
            color: white;
        }
        .icon{
            text-align: 0 .1rem 2rem black;
            
            &:hover{
                transform: scale(1.1);
            }
        }

     
    }

    &__copyright{
        width: 80%;
        display: flex;
        justify-content: center;
        align-items: flex-start;
        height: 5rem;
        color: white;
        font-size: 1.6rem;
        font-family: $fontAudio;
        border-top: .1rem solid rgba(0, 0, 0, 0.425);
        padding: 2rem;
        
    }
}