.header {
    width: 100%;
    background-color: $color-black;
    height: auto;
    position: relative;
    padding: 5rem;
    display: flex;
    align-items: center;

    img {
        width: 60%;
        transform: scale(1);
        height: auto;
        animation: imgScale 10s linear 2s infinite alternate;
    }

    h1{
        position: absolute;
        width:40%;
        right: 3%;
        text-align: right;
        font-weight: bold;
        letter-spacing: .2rem;
        line-height: 1.2;
        transform: scale(.9);
        color:$color-blue;
        font-size: 8rem;
        text-shadow: 0 .2rem 2rem rgba(255, 255, 255, 0.219);
        animation: textScale 15s linear 2s infinite alternate;
        
        span{
            color: white;
          
        }
    }


}





//Animation
@keyframes imgScale {
    from {
        transform: scale(.9);
    }

    to {
        transform: scale(1);
    }
}

@keyframes textScale {
    from {
        right: 5%;
    }

    to {
        right: 15%;
    }
}